* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
.container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  
}
.headcontainer {
  height: 80vh;
  color: white;
  width: 25%;
  border-radius: 11px;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  overflow-y: scroll;
}
.refresh {
  text-align: right;
  margin-bottom: 30px;
  color: #8566aa;

}
.date {
  margin-top: 25px;
  text-align:center;
  color: white;
  font-weight: 600;
  font-size: 1.2rem;
  word-spacing: 0.2rem;
  letter-spacing: 0.1rem;
  font-family: cursive;
  padding: 6px 0 9px 0;
  background-color: #8566aa;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
  overflow-y: hidden;
  
}
.inputblock {
  margin: auto;
  height: 7vh;
  width: 65%;
  display: flex;
  margin-top: 20px;
  justify-content: center;
  align-items: center;
  
}
.inputblock input {
  margin-top: 25px;
  background: none;
  outline: none;
  border: none;
  color: #8566aa;
  font-family: cursive;
  width: 80%;
  margin-left: 10px;
  padding-bottom: 5px;
  font-size: 1.2rem;
  font-weight: 550;
  text-align: center;
  border-bottom: 3px solid blueviolet;
}

.inputblock button {
  border: none;
  border-radius: 50%;
  margin-top: 21px;
  font-size: 25px;
  font-weight: 700;
  color: white;
  background-color: #8566aa;
  width: 12%;
  height:58%;
 
}
.inputblock button:hover {
  background-color: #20bf6b;
}
ol {
  
  margin-top: 30px;
  

  
}
.todostyle li {
  display: flex;
  font-size: 20px;
 
  padding-left: 0px;
  font-family: cursive;
  min-height: 40px;
  margin-top: 10px;
  width: 80%;
  align-items: center;
  text-transform: capitalize;
 
  
  margin-left: 5px;
  color: #8566aa;
}
.fa {
  color: #8566aa;
  font-size: 1.2rem;
  font-weight: 600;
}
.fa:hover {
  color: red;
}
.todostyle {
 margin-left: 10px;
  border-bottom: 2px solid grey;
  max-width: 80%;
  text-align: left;
  display:flex;
  
  justify-content: space-between;
  align-items: center;
 
}
.delete {
  margin-top: 10px;
  
}
@media(max-width:1100px) {
  .headcontainer {
    height: 70vh;
    width: 35%;
    border-radius: 11px;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
    overflow-y: scroll;
    
  }
  .todostyle {
    max-width: 80%;
  }
  .fa {
    font-size: 1.1rem;
    font-weight: 500;
  }
  .todostyle li {
    
    font-size: 18px;
   
    padding-left: 0px;
    font-family: cursive;
    min-height: 30px;
    margin-top: 7px;
    width: 80%;
    align-items: center;
    text-transform: capitalize; 
    margin-left: 3px;
    
  }
  .inputblock {
    
    height: 5vh;
    width: 55%;
    display: flex;
    margin-top: 12px;
    justify-content: center;
    align-items: center;
    
  }
  .inputblock button {
    
    margin-top: 12px;
    font-size: 20px;
    font-weight: 600;
    color: white;
    background-color: #8566aa;
    width: 12%;
    height:53%;
   
   
  }
  .inputblock input {
    margin-top: 18px;
   
    width: 60%;
    margin-left: 7px;
    padding-bottom: 3px;
    font-size: 1rem;
    font-weight: 500;
    border-bottom: 3px solid blueviolet;
  }
  
}
@media (max-width : 850px) {
  .headcontainer {
    width: 37%;
  }
}
@media (max-width : 710px) {
  .headcontainer {
    width: 47%;
  }
}
@media (max-width : 550px) {
  .headcontainer {
    width: 60%;
  }
}
@media (max-width : 400px) {
  .headcontainer {
    width: 75%;
  }
}


